@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.wrapper{width:90%;margin:0 auto}body{font-family:'Nunito sans';background-color:#202c37;transition:.5s ease-in-out all}body.darkMode{background-color:#fff}a{color:inherit}h1,h2,h3{color:#fff}button{background-color:#2b3945;border:none;outline:none;height:3.2em;width:30em;box-shadow:0 0 20px -10px black;padding:0 1em;color:#FFF}button:hover{cursor:pointer}

header{background-color:#2b3945;color:#fff;font-size:.8em;height:5em;display:flex;align-items:center;justify-content:space-between;font-weight:300;transition:.5s ease-in-out all}body.darkMode header{background-color:#Fff}body.darkMode header a,body.darkMode header i,body.darkMode header span{color:#000}header nav{display:flex;align-items:center;justify-content:space-between}header nav span:hover{cursor:pointer}header .fas,header .far{margin-right:.5em}

.MainSearch{margin-top:2.5em;display:flex;justify-content:space-between}.MainSearch__country input{background-color:#2b3945;border:none;outline:none;height:3.2em;width:30em;box-shadow:0 0 20px -10px black;padding:0 1em;color:#FFF;border-radius:5px;transition:.5s ease-in-out all}.MainSearch__country input::-webkit-input-placeholder{color:#FFF}.MainSearch__country input:-ms-input-placeholder{color:#FFF}.MainSearch__country input::-ms-input-placeholder{color:#FFF}.MainSearch__country input::placeholder{color:#FFF}.MainSearch select{background-color:#2b3945;border:none;outline:none;height:3.2em;width:15em;box-shadow:0 0 20px -10px black;padding:0 1em;color:#FFF;border-radius:5px;transition:.5s ease-in-out all}.MainSearch option{font-family:'Nunito Sans';line-height:2em;height:2em;font-size:1.2em}body.darkMode .MainSearch input,body.darkMode .MainSearch select{background-color:#fff;color:#000}body.darkMode .MainSearch input::-webkit-input-placeholder, body.darkMode .MainSearch select::-webkit-input-placeholder{color:#000}body.darkMode .MainSearch input:-ms-input-placeholder, body.darkMode .MainSearch select:-ms-input-placeholder{color:#000}body.darkMode .MainSearch input::-ms-input-placeholder, body.darkMode .MainSearch select::-ms-input-placeholder{color:#000}body.darkMode .MainSearch input::placeholder,body.darkMode .MainSearch select::placeholder{color:#000}@media screen and (max-width: 768px){.MainSearch{flex-direction:column}.MainSearch__country input{margin-bottom:2em;width:90%}}

.Country{width:100%;margin:1em 0;margin:auto auto;background-color:#2b3945;transition:0.5s ease-in-out all;will-change:0.5s ease-in-out all}.Country__flag{color:#FFF}.Country__flag img{width:100%;height:10em;object-fit:cover}.Country__info{padding:.5em 1em;color:#FFF}.Country__info strong{color:rgba(255,255,255,0.9);font-weight:600}.Country__info h2{font-weight:800;font-size:1.2em}.Country__info h3{font-size:1em;line-height:50%;font-weight:300}body.darkMode .Country{background-color:#FFF;color:#000}body.darkMode .Country__info h3,body.darkMode .Country h2,body.darkMode .Country strong{color:#000}

.Loading{position:fixed;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,0.5)}.Loading__main{position:relative;top:calc(50% - 12.5%);width:14em;height:25%;text-align:center;margin:auto auto;display:flex;flex-wrap:wrap}.Loading__main__square{height:3em;width:3em;background-color:#fff;margin:5px;-webkit-animation:loader alternate infinite 1s ease-in-out;animation:loader alternate infinite 1s ease-in-out}@-webkit-keyframes loader{from{opacity:0;transform:rotate(0deg)}to{opacity:1;transform:rotate(360deg);transform-origin:center}}@keyframes loader{from{opacity:0;transform:rotate(0deg)}to{opacity:1;transform:rotate(360deg);transform-origin:center}}

.Countries{margin-top:3em;display:grid;grid-template-columns:repeat(auto-fit, minmax(20%, 1fr));grid-gap:4em;gap:4em}@media screen and (max-width: 768px){.Countries{grid-template-columns:1fr}}

.CountryInfo{margin-top:2em}.CountryInfo__flag{width:45%;margin-right:8em}.CountryInfo__details{margin-top:3em;display:flex;align-items:center}.CountryInfo__details img{width:100%;object-fit:cover}.CountryInfo__details__secondary{display:grid;grid-template-columns:1fr 1fr;width:100%;grid-template-areas:'details detailsRigth' 'borders borders'}.CountryInfo__details__secondary__details{grid-area:details}.CountryInfo__details__secondary__rigth{justify-self:flex-end;grid-area:detailsRigth}.CountryInfo__details__secondary h3,.CountryInfo__details__secondary strong{font-size:1em}.CountryInfo__details__secondary h3{font-weight:300}.CountryInfo__details__secondary strong{font-weight:600}.CountryInfo__borders{margin-top:1.5em;display:flex;flex-wrap:wrap;align-items:center;grid-area:borders}.CountryInfo #backButton{width:9em;height:2.5em}.CountryInfo #borderButton{width:6em;height:2em;margin:0 .2em}body.darkMode .CountryInfo #backButton{background-color:#FFF;color:#000}body.darkMode .CountryInfo #borderButton{background-color:#FFF;color:#000}body.darkMode .CountryInfo h3{color:#000}body.darkMode .CountryInfo__datails_secondary h3,body.darkMode .CountryInfo strong,body.darkMode .CountryInfo h1{color:#000}@media screen and (max-width: 768px){.CountryInfo__details{flex-direction:column}.CountryInfo__flag{width:100%;margin:0 auto}.CountryInfo__details__secondary{grid-template-columns:1fr;grid-template-areas:'details' 'detailsRigth' 'borders'}.CountryInfo__details__secondary__rigth{margin-top:2em;justify-self:flex-start}}

